const DrawnTimerDelay = 100;
export function createFnAfterTimeoutExecutor(handler) {
    let timerId = -1;
    return {
        execute: function () {
            this.reset();
            timerId = setTimeout(handler, DrawnTimerDelay);
        },
        reset: function () {
            if (timerId !== -1)
                clearTimeout(timerId);
        },
    };
}
